import Content from 'components/ui/Content';
import Typography from 'components/ui/Typography';

import classNames from 'classnames';

import type { TDiagnosisResultCustomer } from '../types';
interface DiagnosisResultCustomerProps {
  data: TDiagnosisResultCustomer[];
  responseId: string;
}

const COLOR_MAPS = {
  'Active Accumulator': 'bg-red-default',
  'Independent Individualist': 'bg-yellow-default',
  'Friendly Follower': 'bg-green-default',
  'Passive Preserver': 'bg-primary-500'
};

const DiagnosisResultCustomer = ({ data, responseId }: DiagnosisResultCustomerProps) => {
  const result = data.find((item) => item.id === Number(responseId));

  if (!result) {
    return <Typography>결과가 없어요?</Typography>;
  }

  const { type, typeEng, intro, propensity, bias, conclusion } = result;

  return (
    <div className="flex flex-col items-center max-tablet:px-5">
      <Typography size="lg" weight={700} align="center">
        나의 투자 편향 유형은
      </Typography>

      <div
        className={classNames(
          'flex items-center gap-x-3 mt-3 py-2 px-5 rounded-card-xl',
          COLOR_MAPS[typeEng]
        )}
      >
        <Typography size="lg" weight={700} color="text-white">
          {type}
        </Typography>
        <i className="inline-flex w-px h-4 bg-white/30" />
        <Typography color="text-white/80">{typeEng}</Typography>
      </div>

      <Content
        gap={30}
        pt="mobile:pt-[3.25rem] tablet:pt-10"
        pb="mobile:pb-[7rem] tablet:pb-[3.125rem]"
        px="px-0"
      >
        <Content.Section>
          <Content.SectionTitle variant="body" size="base">
            {type} 투자 편향 소개
          </Content.SectionTitle>
          <div className="flex flex-col gap-y-8">
            {intro.map((_item, index) => (
              <Typography key={index} color="text-gray-700">
                {_item}
              </Typography>
            ))}
          </div>
        </Content.Section>

        <Content.Section gap={12}>
          <Content.SectionTitle variant="body" size="base">
            {type} 투자 성향
          </Content.SectionTitle>
          <ul role="list" className="qb-marker">
            {propensity.map((_item, index) => (
              <li key={index}>
                <Typography color="text-gray-700">{_item}</Typography>
              </li>
            ))}
          </ul>
        </Content.Section>

        <Content.Section gap={12}>
          <Content.SectionTitle variant="body" size="base">
            {type} 주요 투자 편향
          </Content.SectionTitle>
          <div className="qb-gray-box flex flex-col items-stretch py-4 px-3 bg-gray-100 rounded-card-md">
            <ul className="flex flex-col gap-y-[0.375rem]">
              {bias.map((_item, index) => (
                <li key={index} className="flex">
                  <Typography weight={500} color="text-gray-700" className="shrink-0">
                    {`${index + 1}.`}
                  </Typography>
                  <div className="inline-flex grow ml-2">
                    <Typography weight={500} color="text-gray-700">
                      {_item}
                    </Typography>
                  </div>
                </li>
              ))}
            </ul>
          </div>
          <div className="flex flex-col gap-y-8">
            {conclusion.map((_item, index) => (
              <Typography key={index} color="text-gray-700">
                {_item}
              </Typography>
            ))}
          </div>
        </Content.Section>
      </Content>
    </div>
  );
};

export default DiagnosisResultCustomer;
